
.benefits {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(0deg, #1a1826, #413b5e);
  padding-bottom: 100px;
}

.benefits p {
  color: #f1f1f1;
  padding-top: 20px;
  font-size: 1.4em;
}
h2 {
  font-size: 3em;
  font-weight: 400;
  color: #f1f1f1;
}

textarea {
  border: 1px solid #413b5e;
    color: #f1f1f1;
    resize: none;
    width: 90%;
    margin-top: 15px;
    background: none;
    font-size: 1.4em;
}
.caja_comentarios {
  display: grid;
  place-items: center;
  margin-bottom: 15px;
  margin-top: 25px;
}
button {
  width: 20%;
  height: 40px;
  border-radius: 4px;
  background: none;
  border: 1px solid #7060c0;
  margin-top: 15px;
  color: #f1f1f1;
}

.btn_eliminar {
  width: auto;
  padding: 5px;
  margin-left: 15px;
}

.btn_eliminar:hover {
  background-color: red;
}

.benefitTitle {
  font-size: 2.5em;
  font-weight: 400;
  color: #f1f1f1;
}

.cajaContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.caja {
  width: calc(33.33% - 20px);
  min-width: 200px;
  max-width: 300px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px;
  padding: 5px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.comentario {
  margin-top: 10px;
}

.imagen {
  width: 50%;
  height: auto;
  border-radius: 0px;
}

.img {
  width: 100%;
  max-width: 100%;
  height: auto;
  border-radius: 50px;
}


@media (max-width: 768px) {
  .caja {
    width: calc(50% - 20px);
  }
}

@media (max-width: 480px) {
  .caja {
    width: 100%;
    max-width: 100%;
  }
}
